@import url('https://fonts.googleapis.com/css?family=Rajdhani:400,500,600,700');

html{
    font-size: 6px;
}

body {
    font-family: 'Rajdhani', sans-serif;
    font-weight: 300;
    font-size: 2rem;
    line-height: 1.33;
    color: #6d6e70;
    text-align: center;
    background-color: #f5f5f5;
    overflow-x: hidden;
}

.center-img {
    margin: auto
}

.container {
    margin-bottom: 4rem;
}

ul, ol {
    margin-bottom: 0;
}

/*   Font colors for good and bad values  ------------------------------ */

.text-orange {
    color: #f7931d;
}

.text-white {
    color: #fff;
}

.site-value-bad {
    color: #e32527;
}

/*   Menu  ------------------------------ */

.efficiency-nav {
    width: 100%;
    background-color: #283139;
    margin-bottom: 0;
}

.efficiency-menu li{
    padding: 1rem;
    font-size: 3rem;
}

.efficiency-menu a {
    text-decoration: none;
    color: #fff;
    font-weight: 600;
    &:hover {
        text-shadow: 1px 1.7px 4px rgba(0, 0, 0, 0.35);
        color: #f7931d;
    }
}

/*   Banner  ------------------------------ */

.banner {
    margin-top: 218px;
    color: #fff;
    height: 600px;
    background-image: linear-gradient(155deg, #4c5d6c, #283139);
}

.banner-head {
    padding-top: 5rem;
}

.banner-title {
    font-size: 5.5rem;
    font-weight: 400;
    text-shadow: 1.5px 2.6px 0 rgba(0, 0, 1, 0.25);
    &:after {
        position: absolute;
        content: '';
        margin: 9rem 0;
        left: 50%;
        transform: translateX(-50%);
        width: 60px;
        height: 1px;
        background-color: #fff;
    }
}

.site-name {
    font-size: 3.8rem;
    margin: 3rem 0 0;
}

.site-value {
    font-size: 16rem;
    font-weight: 700;
    margin-top: 0;
    color: linear-gradient(155deg, #ffffff, #e1e1e1);
    text-shadow: 1.5px 2.6px 0 rgba(0, 0, 0, 0.25);
} 

.progress {
    height: 10px;
}

.your-store-value {
    padding: 2.5rem 0;
    font-size: 6rem;
    font-weight: 700;
    text-shadow: 1.5px 2.6px 0 rgba(0, 0, 0, 0.25);
}

.your-store-p {
    margin: 2rem;
    p {
        font-size: 2rem;
        margin: 0;
        padding: 0;
    }
}

.own-site-pic {
    box-shadow: 3.1px 5.1px 21px 0 rgba(175, 100, 12, 0.25);
}

/*   Progress bar   */

.result-ul li{
    display: inline-block;
}

/*   Taffic Section  ------------------------------ */

.traffic-head {
    margin-top: 18rem; 
}

.module-head {
    position: relative;
    padding: 2.5rem;
    
    &:before {
        position: absolute;
        content:'';
        width: 100%;
        height: 24px;
        z-index: 1;
        top: 4.8rem;
        left: 0;
        opacity: .4;
        background: repeating-linear-gradient(
            -45deg,
            #f7931d,
            #f7931d 2px,
            #f5f5f5 2px,
            #f5f5f5 9px
        );
    }
}

.module-head-title {
    display: inline-block;
    background: #f5f5f5;
    position: relative;
    padding: 0 3rem;
    z-index: 2;
    font-size: 5.2rem;
    font-weight: 700;
    text-shadow: 1px 1.7px 0 #ffffff;
}

/*  Traffic Rating Cards  */

.traffic-card-title, .topics-head-title {
    font-size: 2.4rem;
}

.topics-head {
    padding: 6rem;
}

.traffic-card-p, .topics-head-p {
    font-size: 1.6rem;
    margin: 0;
    padding: 0;
}

.site-value-h4 {
    font-size: 4.2rem;
    font-weight: 700;
}

.site-rating {
    padding: 4rem;
}

.site-rating + .site-rating {
    border-top: 1px solid #394856;
}


/*   Money  ------------------------------ */

.money {
    padding: 4rem 2rem;
    background-color: #fff;
    border-bottom: 2px solid #f7931d;
    border-top: 2px solid #f7931d;
}

.map-head {
    padding: 4rem;
}

.module-foot {
    padding: 4rem 0;
}

/*   Competitors  ------------------------------ */

.competitor-screens {
    padding: 4rem;
}


/*   Recover these competitors  ------------------------------ */

.blue-bg {
    background-color: #353f4e;
    height: 100%;
}

.inverse-module-head {
    &:before {
        position: absolute;
        content:'';
        width: 100%;
        height: 24px;
        z-index: 1;
        top: 8rem;
        background: repeating-linear-gradient(
            -45deg,
            #f7931d,
            #f7931d 2px,
            #353f4e 2px,
            #353f4e 9px
        );
    }
}

.inverse-module-head-title {
    background-color: #353f4e;
    text-shadow: 1.5px 2.6px 0 rgba(0, 0, 0, 0.25);
}

.recover p {
    color: #fff;
}

.competitor-screen {
    box-shadow: 4.1px 6.9px 10px 0 rgba(0, 0, 0, 0.2);
    border-radius: 5px;
}

.table-wrap {
    margin: auto 1rem;
}

.advice-table {
    margin: auto;
    border-collapse: separate;
}

tr td{
    padding: 1rem 3rem;
    border-bottom: 1px solid #777
}

th {
    opacity: .6;
    font-weight: 400;
    text-transform: uppercase;
    padding: 0 3rem;
}

/*   ORG  ------------------------------ */

.ebook-pic {
}

.ebook-pic ul {
    margin: 4rem;
    list-style: none;
}

.ebook-pic li {
    padding: 3rem 0 3rem 5.5rem;
    font-size: 2.5rem;
    text-shadow: 0.5px 0.9px 0 rgba(0, 0, 0, 0.25);
    &:before {
        content: url('/img/done.svg');
        position: absolute;
        left: 0;
        margin-top: -1.5rem;
        margin-left: 4rem;
    }
}

.ebook-download-btn {
    margin-top: 10rem;
    margin-bottom: 6rem;
}
.ebook-download-btn a {
    margin-left: 4rem;
    color: #f7931d;
    border: 2px solid #f7931d;
    padding: 3rem 5rem;
    font-size: 3rem;
    text-transform: uppercase;
    text-decoration: none;
    &:hover {
        background-color: #f7931d;
        color: #fff;
    }
}

/*   Footer  ------------------------------ */

footer {
    background-color: #283139;
}

.footer-p {
    font-size: 1.4rem;
}






















/*   Media Querri's   */

@media screen and ( min-width: 576px ){
    html {
        font-size: 7px;
    }
}

@media screen and ( min-width: 768px ){
    html {
        font-size: 8px;
    }


}

@media screen and ( min-width: 992px ){
    html {
        font-size: 8px;
    }

    /*   Traffic  ------------------------------ */    

    .traffic-head {
        margin-top: 28rem; 
    }

    .module-head {
        &:before {
            top: 4.4rem;
            height: 34px;
        }
    }

    .module-head-title {
        padding: 0 5rem;
    }

    .site-rating + .site-rating {
        border-left: 1px solid #394856;
        border-top: none;
    }

}

@media screen and ( min-width: 1200px ){

    html {
        font-size: 10px;
    }

    /*   Menu  ------------------------------ */

    .efficiency-menu {
        padding-top: 3rem;
        padding-bottom: 3rem;
        li {
            display: inline;
            padding: 0 3rem;
            font-size: 2.2rem;
        }
    }

    /*   Banner  ------------------------------ */    

    .banner {
        margin-top: 80px;
        height: 840px;
    }

    /*   Traffic  ------------------------------ */    

    .traffic-head {
        margin-top: 28rem; 
    }

    .module-head {
        &:before {
            top: 4.4rem;
            height: 34px;
        }
    }

    .module-head-title {
        padding: 0 5rem;
    }

    .site-rating + .site-rating {
        border-left: 1px solid #394856;
        border-top: none;
    }

    /*   Money background styles  ------------------------------ */  
    
    .money {
        position: relative;
        &:before {
            position: absolute;
            content: '';
            height: 40px;
            width: 2px;
            background-color: #f7931d;
            left: 0;
            top:0;
        }

        &:after {
            position: absolute;
            content: '';
            height: 40px;
            width: 2px;
            background-color: #f7931d;
            right: 0;
            top:0;
        }
    }

    .white-bg {
        position: relative;
    }
    .money-bg {
        &:before {
            content: '';
            position: absolute;
            width: 100%;
            height: 1260px;
            background-color: #ffffff;
            left: 0;
            margin-top: -150rem;
            z-index: -1;
        }
        
    }

    /*   Recover these competitors  ------------------------------ */

    .competitor-foot, .competitor-screens {
        color: #fff;
    }

    /*   ORG  ------------------------------ */

    .ebook-pic {
        margin: 0;
    }
    .ebook-pic li {
        padding: 1.4rem 0 1.4rem 5.5rem;
        font-size: 2rem;
    }
    .ebook-pic li:before {
        margin-top: -0.5rem;
    }

    .ebook-download-btn {
        margin-top: 2rem;
    }
    .ebook-download-btn a { 
        color: #f7931d;
        border: 2px solid #f7931d;
        padding: 1.6rem 6rem;
        font-size: 2.4rem;
        text-transform: uppercase;
        text-decoration: none;
        &:hover {
            background-color: #f7931d;
            color: #fff;
        }
    }

    .foot-bg {
        background-color: #353f4e;
        width: 100%;
        height: 300px;
        position: absolute;
        margin-top: -30rem;
        z-index: -1;
    }
}